"use client";

import { FormEvent, useState } from "react";
import styles from "./create.module.css";

interface Option {
  id: number;
  text: string;
}

interface Question {
  id: number;
  title: string;
  options: Option[];
}

export default function Create() {
  const [title, setTitle] = useState("");
  const [questions, setQuestions] = useState<Question[]>([]);

  const addQuestion = () => {
    setQuestions((prev) => [
      ...prev,
      { id: Date.now(), title: "", options: [] },
    ]);
  };

  const removeQuestion = (id: number) => {
    setQuestions((prev) => prev.filter((q) => q.id !== id));
  };

  const updateQuestionTitle = (id: number, value: string) => {
    setQuestions((prev) =>
      prev.map((q) => (q.id === id ? { ...q, title: value } : q))
    );
  };

  const addOption = (questionId: number) => {
    setQuestions((prev) =>
      prev.map((q) =>
        q.id === questionId
          ? { ...q, options: [...q.options, { id: Date.now(), text: "" }] }
          : q
      )
    );
  };

  const removeOption = (questionId: number, optionId: number) => {
    setQuestions((prev) =>
      prev.map((q) =>
        q.id === questionId
          ? { ...q, options: q.options.filter((o) => o.id !== optionId) }
          : q
      )
    );
  };

  const updateOptionText = (
    questionId: number,
    optionId: number,
    value: string
  ) => {
    setQuestions((prev) =>
      prev.map((q) =>
        q.id === questionId
          ? {
              ...q,
              options: q.options.map((o) =>
                o.id === optionId ? { ...o, text: value } : o
              ),
            }
          : q
      )
    );
  };

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
  };

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Create Quiz</h1>
      <form className={styles.form} onSubmit={handleSubmit}>
        <input
          className={styles.input}
          placeholder="Quiz Title"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
        />

        {questions.map((question) => (
          <div key={question.id} className={styles.questionCard}>
            <input
              className={styles.input}
              placeholder="Question Title"
              value={question.title}
              onChange={(e) => updateQuestionTitle(question.id, e.target.value)}
            />

            {question.options.map((option) => (
              <div key={option.id} className={styles.optionRow}>
                <input
                  className={styles.input}
                  placeholder="Option Text"
                  value={option.text}
                  onChange={(e) =>
                    updateOptionText(question.id, option.id, e.target.value)
                  }
                />
                <button
                  type="button"
                  className={styles.deleteButton}
                  onClick={() => removeOption(question.id, option.id)}
                >
                  X
                </button>
                <TbTrash
                  className={styles.deleteIcon}
                  size={24}
                  onClick={() => deleteQuiz(quiz.uuid)}
                />
              </div>
            ))}
            <button
              type="button"
              className={styles.addButton}
              onClick={() => addOption(question.id)}
            >
              Add Option
            </button>
          </div>
        ))}

        <button
          type="button"
          className={styles.addButton}
          onClick={addQuestion}
        >
          Add Question
        </button>
        <button type="submit" className={styles.button}>
          Save Quiz
        </button>
      </form>
    </div>
  );
}
